k's Kickstarter campaign goes live Tuesday the 17th of July\n                    Media resources are available with the press kit at\n                    https://bit.ly/2JqLtrU\n                ")]),_vm._v(" "),_c('h4',[_vm._v("\n                    Quoted individuals\n                ")]),_vm._v(" "),_c('ul',{staticClass:"padding-bottom-3x"},[_c('li',[_vm._v("\n                        Joel Hernández, Founder of Openbook, Jaya Baloo, Chief Operating Officer.\n                    ")])]),_vm._v(" "),_c('h4',{staticClass:"padding-top-2x"},[_vm._v("\n                    Openbook; Social Network 2.0\n                ")]),_vm._v(" "),_c('p',[_vm._v("\n                    Openbook, based in the Netherlands is a new kind of social network. One that is privacy-friendly, customizable to a large extent, easy to move from existing networks and has a strong focus in fun.\n                ")]),_vm._v(" "),_c('blockquote',[_vm._v("\n                    “One core difference with other social networks is that we put privacy first. We don’t track anything you do or use your data for any other purpose than providing the social networking functionality. We’re also fully transparent about this, all of our code is open-source, so people can see that we do what we say we do. We make the trust our users put in us verifiable.”\n                ")]),_vm._v(" "),_c('p',[_vm._v("\n                    Openbook will include all features one would expect from a social network, plus it allows its users to customise their own profile. “We’re all different and that is something to be celebrated and embraced. Openbook does this by allowing our users to personalise almost everything visual on the network.” Openbook also makes the switch from other social network as easy as possible. “With our simple drag-and-drop tool you can easily transfer the data from your old social networks”. As for the revenue model, it is said to be based on transactions between users.\n                ")]),_vm._v(" "),_c('blockquote',[_vm._v("\n                    “We are really focused on building a better social network than anything that’s out there, not only in terms of security & privacy, but experience as well. Once we achieve that, we want to create a way for transactions to occur between users. This will then be used to create a marketplace for products and later on a marketplace for services. Our revenue model will never be advertisement.”\n                ")]),_vm._v(" "),_c('p',[_vm._v("\n                    The team behind Openbook consists of people who believe in the main values Openbook stands for: honesty, respect for users, togetherness, altruism and fun. It a diverse team full of talented people like internet hall of fame member and cryptography pioneer and creator of PGP Phil Zimmermann, and internationally recognised security experts like Jaya Baloo.\n                ")]),_vm._v(" "),_c('blockquote',[_vm._v("\n                    “We decided to go for crowdfunding because we want the early decisions, the ones that will dictate the future of Openbook to be driven by people, not capital.”\n                ")]),_vm._v(" "),_c('blockquote',[_vm._v("\n                    Jaya adds to Joel’s comments “We are designing a solution for good, a social haven where the user community is positively empowered to share their data securely.”\n                ")]),_vm._v(" "),_c('p',[_vm._v("\n                    Openbook aims to raise 100K in one month time. The money will be used for amongst others further development of Openbook. The Kickstarter campaign can be found at https://open-book.org/kickstarter .\n                ")]),_vm._v(" "),_c('p',[_vm._v("\n                    If you want to learn more about it or keep up with the developments of Openbook, you can find them online at https://open-book.org or at Twitter @Openbook_org.\n                ")])]),_vm._v(" "),_c('div',{staticClass:"content is-medium"},[_c('h3',[_vm._v("\n                    Press release: Openbook; Social Network 2.0\n                ")]),_vm._v(" "),_c('h5',{staticClass:"has-text-weight-normal"},[_vm._v("\n                    Tuesday, 5th of June, 2018\n                ")]),_vm._v(" "),_c('p',[_vm._v("\n                    Press Release Openbook. Website Open / Countdown to Kickstarter Campaign 15th of July\n                ")]),_vm._v(" "),_c('h4',[_vm._v("\n                    Context:\n                ")]),_vm._v(" "),_c('p',[_vm._v("\n                    Openbook will launch Tuesday 5th of June it’s new website including in depth information about\n                    the\n                    project, a revised manifesto, team members and its countdown to its crowdfunding campaign.\n                    Preliminary screenshots and media resources are available on the press kit at\n                    https://bit.ly/2L9kZwj.\n                ")]),_vm._v(" "),_c('h4',[_vm._v("\n                    Quoted individuals\n                ")]),_vm._v(" "),_c('ul',{staticClass:"padding-bottom-3x"},[_c('li',[_vm._v("\n                        Joel Hernández, Founder of Openbook.\n                    ")])]),_vm._v(" "),_c('h4',{staticClass:"padding-top-2x"},[_vm._v("\n                    Openbook; Social Network 2.0\n                ")]),_vm._v(" "),_c('p',[_vm._v("\n                    A social network start-up based in The Netherlands, soon to start its crowdfunding campaign on\n                    the\n                    15thof July on the platform Kickstarter, claims to be the answer to the rising concerns of\n                    social\n                    media.\n                ")]),_vm._v(" "),_c('blockquote',[_vm._v("\n                    “Openbook is a step forward in the evolution of social networks. Day by day we find ourselves\n                    struggling and failing to deal with the shortcomings of the first generation of social networks.\n                    Complete lack of privacy, unethical business models based on personal information, the\n                    increasing\n                    social divide fuelled by digital echo chambers, user experiences designed to keep us attached to\n                    our\n                    screens. We are fixing all of that and more.”\n                ")]),_vm._v(" "),_c('p',[_vm._v("\n                    The source code for the social network is also said to be open-source, meaning free for anyone\n                    to\n                    download, inspect and reproduce.\n                ")]),_vm._v(" "),_c('blockquote',[_vm._v("\n                    “Social networks play such an important role in our lives. We need to be able to trust that they\n                    are\n                    doing the right things. By making everything open source we make that trust verifiable.”\n                ")]),_vm._v(" "),_c('p',[_vm._v("\n                    As for what features to expect, it’s said it will include all the features you would expect from\n                    a\n                    social network like timelines, posts and messaging. Furthermore, it’s said users will be able to\n                    migrate from other social networks seamlessly.\n                ")]),_vm._v(" "),_c('blockquote',[_vm._v("\n                    “We like you, have put a tremendous amount of our time and data into current social networks. It\n                    would be a shame to start all over. That is why we are building data migrators. Moving into\n                    Openbook\n                    will be seamless.”\n                ")]),_vm._v(" "),_c('blockquote',[_vm._v("\n                    “Another core component of Openbook is that its revenue model is sustainable and ethical. We do\n                    not\n                    do ads nor will we ever. Ads are the reason social networks started to collect increasingly more\n                    information about their users in order to serve them personalized ads and increase revenue. This\n                    combined with the social network’s little interest to protect the collected information is what\n                    gave\n                    rise to the loss of privacy and the inability to fix the problem. The revenue model of current\n                    social networks is the problem itself.”\n                ")]),_vm._v(" "),_c('p',[_vm._v("\n                    The revenue model of Openbook is a mixture between helping the enterprise set their own private\n                    social network for their employees and an open marketplace, initially for products and then\n                    services.\n                ")]),_vm._v(" "),_c('blockquote',[_vm._v("\n                    “The world is a marketplace, so is Openbook. Where other marketplaces have been for physical\n                    products only, we will bring peer to peer services into the platform. For example, you could\n                    sell\n                    your beloved Star Wars collection on the network in the same way you could hire someone to walk\n                    your\n                    dog or be the DJ at your party.”\n                ")]),_vm._v(" "),_c('p',[_vm._v("\n                    And Joel is not alone in this journey. In a diverse team ranging from cyber-security experts\n                    like\n                    Jaya Baloo to internet hall of fame member and cryptography pioneer Phil Zimmermann, Openbook\n                    has a\n                    talented team ready to take on the challenge.\n                ")]),_vm._v(" "),_c('blockquote',[_vm._v("\n                    “It won't be an easy task and we will need all the help we can get. Currently, we are completely\n                    self-funded and most of us work part-time on the project. If we were to continue this way, it\n                    would\n                    take us years to build Openbook. We decided to go for crowdfunding because we want this to be a\n                    project driven by people not by capital. It makes such a big difference on the decisions taken.”\n                ")]),_vm._v(" "),_c('p',[_vm._v("\n                    The crowdfunding campaign for Openbook will take place in the Kickstarter platform, on the\n                    15thof\n                    July, 2018.\n                ")]),_vm._v(" "),_c('p',[_vm._v("\n                    If you want to learn more about it or keep up with the developments of Openbook, you can find them\n                    online at https://open-book.org or at Twitter @Openbook_org.\n                ")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('section',{staticClass:"section",attrs:{"id":"inquiries"}},[_c('div',{staticClass:"small-container"},[_c('div',{staticClass:"content is-medium"},[_c('p',[_vm._v("\n                    Missing something? Contact "),_c('a',{attrs:{"href":"mailto:lambrecht@open-book.org?subject=Press%20inquiry&body="}},[_vm._v("Lambrecht Wessels")]),_vm._v(".\n                ")])])])])}]

